import React, { useRef } from 'react';
import { motion, useTransform, useSpring, useMotionValue } from 'framer-motion';
import styles from './Hero.module.css';
import heroImage from '../assets/1.jfif';

const Hero = () => {
    const ref = useRef(null);

    // Mouse tilt effect
    const x = useMotionValue(0);
    const y = useMotionValue(0);

    const mouseXSpring = useSpring(x);
    const mouseYSpring = useSpring(y);

    const rotateX = useTransform(mouseYSpring, [-0.5, 0.5], ["15deg", "-15deg"]);
    const rotateY = useTransform(mouseXSpring, [-0.5, 0.5], ["-15deg", "15deg"]);

    const handleMouseMove = (e) => {
        const rect = e.currentTarget.getBoundingClientRect();
        const width = rect.width;
        const height = rect.height;

        const mouseX = e.clientX - rect.left;
        const mouseY = e.clientY - rect.top;

        const xPct = mouseX / width - 0.5;
        const yPct = mouseY / height - 0.5;

        x.set(xPct);
        y.set(yPct);
    };

    const handleMouseLeave = () => {
        x.set(0);
        y.set(0);
    };

    return (
        <section className={styles.heroSection} ref={ref}>
            <div className={`container ${styles.heroContainer}`}>

                {/* Left Content */}
                <motion.div
                    className={styles.heroContent}
                    initial={{ opacity: 0, x: -50 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ duration: 0.8, delay: 0.2 }}
                >
                    <motion.h1
                        className={styles.heroTitle}
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.8, delay: 0.4 }}
                    >
                        REFLECT <br />
                        <span className={styles.outlinedText}>FASHION</span>
                    </motion.h1>

                    <motion.p
                        className={styles.heroDescription}
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        transition={{ duration: 0.8, delay: 0.6 }}
                    >
                        Discover a fashion experience that not only mirrors your unique personality
                        but amplifies it. At Reflect, every piece is crafted to elevate your confidence.
                    </motion.p>

                    <motion.div
                        className={styles.heroButtons}
                        initial={{ opacity: 0, y: 20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.8, delay: 0.8 }}
                    >
                        <button className={styles.primaryBtn}>Buy Product</button>
                        <button className={styles.secondaryBtn}>Explore Product</button>
                    </motion.div>
                </motion.div>

                {/* Right Image with 3D Effect */}
                <div
                    className={styles.heroImageWrapper}
                    onMouseMove={handleMouseMove}
                    onMouseLeave={handleMouseLeave}
                >
                    <motion.div
                        className={styles.card3D}
                        style={{
                            rotateX,
                            rotateY,
                            transformStyle: "preserve-3d"
                        }}
                    >
                        <div className={styles.glassEffect}></div>
                        <img src={heroImage} alt="Reflect Fashion Model" className={styles.heroImage} />

                        {/* Floating Elements for Parallax Depth */}
                        <motion.div
                            className={styles.floatingTag}
                            animate={{ y: [0, -10, 0] }}
                            transition={{ repeat: Infinity, duration: 3, ease: "easeInOut" }}
                            style={{ translateZ: "50px" }}
                        >
                            <span>New Arrival</span>
                        </motion.div>
                    </motion.div>
                </div>

            </div>

            {/* Background Decor */}
            <div className={styles.bgGradient}></div>
        </section>
    );
};

export default Hero;
