import React, { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { FiSearch, FiUser, FiShoppingBag, FiMenu, FiX } from 'react-icons/fi';
import styles from './Navbar.module.css';

const Navbar = () => {
    const [scrolled, setScrolled] = useState(false);
    const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
            setScrolled(window.scrollY > 50);
        };
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    const navLinks = [
        { name: 'Home', href: '/' },
        { name: 'Shop', href: '/shop' },
        { name: 'Sale', href: '/sale' },
        { name: 'About Us', href: '/about' },
    ];

    return (
        <>
            <motion.nav
                className={`${styles.navbar} ${scrolled ? styles.scrolled : ''}`}
                initial={{ y: -100 }}
                animate={{ y: 0 }}
                transition={{ duration: 0.5 }}
            >
                <div className={`container ${styles.navContainer}`}>

                    {/* Mobile Menu Button - Left */}
                    <div className={styles.mobileToggle} onClick={() => setMobileMenuOpen(true)}>
                        <FiMenu size={24} />
                    </div>

                    {/* Desktop Menu - Left */}
                    <ul className={styles.navMenu}>
                        {navLinks.map((link) => (
                            <li key={link.name}>
                                <a href={link.href}>{link.name}</a>
                            </li>
                        ))}
                    </ul>

                    {/* Logo - Center */}
                    <div className={styles.logo}>
                        <h1>REFLECT</h1>
                    </div>

                    {/* Icons - Right */}
                    <div className={styles.navIcons}>
                        <FiSearch className={styles.icon} />
                        <FiUser className={styles.icon} />
                        <div className={styles.cartIcon}>
                            <FiShoppingBag className={styles.icon} />
                            <span className={styles.badge}>0</span>
                        </div>
                    </div>
                </div>
            </motion.nav>

            {/* Mobile Menu Drawer */}
            <AnimatePresence>
                {mobileMenuOpen && (
                    <>
                        <motion.div
                            className={styles.overlay}
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            onClick={() => setMobileMenuOpen(false)}
                        />
                        <motion.div
                            className={styles.mobileDrawer}
                            initial={{ x: '-100%' }}
                            animate={{ x: 0 }}
                            exit={{ x: '-100%' }}
                            transition={{ type: 'tween', duration: 0.3 }}
                        >
                            <div className={styles.drawerHeader}>
                                <div className={styles.logo}>REFLECT</div>
                                <div className={styles.closeBtn} onClick={() => setMobileMenuOpen(false)}>
                                    <FiX size={24} />
                                </div>
                            </div>
                            <ul className={styles.mobileLinks}>
                                {navLinks.map((link) => (
                                    <li key={link.name} onClick={() => setMobileMenuOpen(false)}>
                                        <a href={link.href}>{link.name}</a>
                                    </li>
                                ))}
                            </ul>
                        </motion.div>
                    </>
                )}
            </AnimatePresence>
        </>
    );
};

export default Navbar;
