import React from 'react';
import { motion } from 'framer-motion';
import styles from './Marquee.module.css';

const Marquee = () => {
    const marqueeVariants = {
        animate: {
            x: [0, -1000],
            transition: {
                x: {
                    repeat: Infinity,
                    repeatType: "loop",
                    duration: 20,
                    ease: "linear",
                },
            },
        },
    };

    return (
        <section className={styles.marqueeSection}>
            <div className={styles.track}>
                <motion.div
                    className={styles.content}
                    variants={marqueeVariants}
                    animate="animate"
                >
                    <h1>REFLECT FASHION <span className={styles.star}></span> REFLECT FASHION <span className={styles.star}></span> REFLECT FASHION <span className={styles.star}></span> REFLECT FASHION <span className={styles.star}></span></h1>
                    <h1>REFLECT FASHION <span className={styles.star}></span> REFLECT FASHION <span className={styles.star}></span> REFLECT FASHION <span className={styles.star}></span> REFLECT FASHION <span className={styles.star}></span></h1>
                </motion.div>
            </div>
        </section>
    );
};

export default Marquee;
