import React from 'react';
import { motion } from 'framer-motion';
import styles from './DarkSection.module.css';
import heroImage from '../assets/3.jfif';

const DarkSection = () => {
    return (
        <section className={styles.darkSection}>
            <div className={`container ${styles.container}`}>
                <div className={styles.grid}>
                    {/* Text Content */}
                    <motion.div
                        className={styles.textContent}
                        initial={{ opacity: 0, x: -50 }}
                        whileInView={{ opacity: 1, x: 0 }}
                        transition={{ duration: 0.8 }}
                        viewport={{ once: true }}
                    >
                        <h2>CLOTH AND FOOTWEAR <br /> COLLECTION</h2>
                        <p>
                            Reflect offers more than just clothing and footwear  we provide a
                            canvas for your individuality. Our thoughtfully designed apparel and
                            footwear collections blend style and comfort, allowing you to make a
                            statement with every step.
                        </p>
                    </motion.div>

                    {/* Image Content */}
                    <motion.div
                        className={styles.imageContent}
                        initial={{ opacity: 0, x: 50 }}
                        whileInView={{ opacity: 1, x: 0 }}
                        transition={{ duration: 0.8 }}
                        viewport={{ once: true }}
                    >
                        <img src={heroImage} alt="Dark Collection" className={styles.darkImage} />
                    </motion.div>
                </div>
            </div>
        </section>
    );
};

export default DarkSection;
