import React from 'react';
import { motion } from 'framer-motion';
import styles from './Categories.module.css';
import imgWomen from '../assets/2.jfif';
import imgMen from '../assets/3.jfif';
import imgAccessories from '../assets/4.jfif';

const categories = [
    { id: 1, title: 'Women Collection', image: imgWomen, btnText: 'Explore Now' },
    { id: 2, title: 'Women Collection', image: imgMen, btnText: 'Explore Now' },
    { id: 3, title: 'Women Collection', image: imgAccessories, btnText: 'Check Now' },
];

const Categories = () => {
    return (
        <section className={styles.categorySection}>
            <div className={`container ${styles.gridContainer}`}>
                {categories.map((cat, index) => (
                    <motion.div
                        key={cat.id}
                        className={styles.card}
                        initial={{ opacity: 0, y: 50 }}
                        whileInView={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.5, delay: index * 0.2 }}
                        viewport={{ once: true }}
                    >
                        <div className={styles.imageWrapper}>
                            <img src={cat.image} alt={cat.title} className={styles.catImage} />
                            <div className={styles.overlay}></div>
                        </div>

                        <div className={styles.content}>
                            <h3>{cat.title}</h3>
                            <p>Stylish Winter Collection</p>
                            <button className={styles.cardBtn}>{cat.btnText}</button>
                        </div>
                    </motion.div>
                ))}
            </div>
        </section>
    );
};

export default Categories;
